import chart_WeatherLine from '@/components/general/chart_WeatherLine.vue'
export default {
	data() {
		return {
			
		}
	},
	methods: {
		
	},
	onLoad(options) {
		//动态设置标题
		if (options.label) {
		  uni.setNavigationBarTitle({
			title: options.label
		  })
		}
		uni.setNavigationBarColor({
		  frontColor: '#ffffff',
		  backgroundColor: '#48c6ef',
		  animation: {
			duration: 400,
			timingFunc: 'easeIn'
		  }
		})
	},
	mounted(){
		const context = wx.createCanvasContext('Canvas');
		context.beginPath();
		context.moveTo(0, 50);
		context.lineTo(50, 150);
		context.stroke();
		context.closePath();
		context.draw();
	  },
	components:{
		chart_WeatherLine
	}
}
